import useTypedSelector from '../store/selectors';

function useStages() {
  const {
    order: {
      cityId, pointId, carId, dateFrom, dateTo, rateId,
    },
  } = useTypedSelector((state) => state.order);

  //     ,      
  return [
    //      
    //  -    required
    //  -   
    {
      name: '',
      vars: [cityId, pointId],
      buttonLabel: ' ',
    },
    {
      name: '',
      vars: [carId],
      buttonLabel: '',
    },
    {
      name: '',
      vars: [dateFrom, dateTo, rateId],
      buttonLabel: '',
    },
    {
      name: '',
      vars: [],
      buttonLabel: '',
    },
  ];
}

export default useStages;
